* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Spoqa Han Sans Neo';
}
a {
  color: inherit;
  text-decoration: none;
}
a:link {
  text-decoration: none;
}
a:visited {
  text-decoration: none;
}
button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

html{
  scroll-behavior: smooth;
}

.content-box{
  display: flex;
  justify-content: center;
}

.todo-box{
  text-align: center;
  height:50px;
  width:400px;
  border-radius: 10px;
  border-color : pink;
}

.plus-button{
  text-align: center;
  height:50px;
  width:80px;
  border-radius: 10px;
  border-color: pink;
  background-color: bisque;
}